html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 3rem;
}
.header__logo {
  width: 50px;
  height: 50px;
  outline: none;
}
.bm-burger-button {
  position: relative;
}
.bm-burger-button {
  height: 20px;
  width: 45px !important;
}
.bm-burger-bars {
  background: white;
  width: 100%;
  height: 1.2px !important;
}
.bm-cross-button {
  position: relative;
  margin: 3rem;
  padding: 1.5rem;
}
.bm-cross {
  background: #fa923f;
  height: 30px !important;
}
.bm-menu {
  background: #011015;
}
.bm-menu-wrap {
  top: 0;
  width: 100% !important;
}
.bm-item-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.bm-item {
  display: inline-block;
  color: #fa923f;
  text-decoration: none;
  margin:  2rem 0;
  font-size: 1.5rem;
  transition: color 2s;
  outline: none;
}
.bm-item:hover {
  color: pink;
}
@media (min-width: 700px) {
  .header {
    padding: 3rem 5rem;
  }
  .bm-burger-bars {
    background: black;
    height: 1.5px !important;
  }
  .bm-item {
    font-size: 2rem;
  }
}

.footer {
  width: 100%;
  background: #011015;
  color: white;
  font-size: 0.8rem;
  text-align: center;
  padding: 3rem 0;
}
.footer-icons {
  width: 55%;
  margin: 0 auto 2rem;
  display: flex;
  justify-content: space-around;
}
.fa:before {
  font-size: 1.7rem;
  cursor: pointer;
  color: white;
}
@media (min-width: 700px) {
  .footer {
    padding: 3em 10%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: none;
    color: grey;
    text-align: left;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
  }
  .footer-icons {
    width: 18%;
    margin: 0;
    justify-content: space-between;
  }
  .fa:before {
    color: grey;
  }
}

.wrap-white-line {
  opacity: 0;
  -webkit-animation: fade-in-right ease 1s forwards;
          animation: fade-in-right ease 1s forwards;
}
.wrap-white-line {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}
@-webkit-keyframes fade-in-right {
  from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes fade-in-right {
  from {
    opacity: 0;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}


.button {
  color: white;
  margin-top: 2rem;
  font-family: "Poiret One";
  padding: 1rem 2rem;
  border: .6px solid white;
  background: none;
  border-radius: 5rem;
  font-size: 1rem;
  outline: none;
  cursor: pointer;
  transition: 1s;
}
.button:hover {
  color: #fa923f;
  border: .6px solid #fa923f;
}
.picture-section-wrap {
  position: relative;
  height: 700px;
}
.wrap-hello {
  position: absolute;
  color: white;
  left: 0;
  bottom: 0;
  margin: 20% 15%;
}
.hello {
  font-size: 4rem;
}
.hello-subtitle {
  font-size: 1.8rem;
  font-family: "Poiret One";
}
.wrap-img {
  width: 100%;
  height: 100%;
  -webkit-filter: blur(0.5px);
          filter: blur(0.5px);
}
@media (min-width: 700px) {
  .picture-section-wrap {
    width: 65%;
    height: 100vh;
  }
  .wrap-hello {
    margin: 25% 15%;
  }
  .hello {
    font-size: 5rem;
  }
}

.vertical-line {
  display: none;
}
@media(min-width: 700px) {
  .vertical-line {
    display: block;
    width: 1px;
    background: grey;
    height: 100vh;
    margin-left: 4px;
  }
  @-webkit-keyframes grow {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  @keyframes grow {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }
  .vertical-line {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-animation: grow cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s forwards;
            animation: grow cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s forwards;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
  }
}

.description-section-wrap {
  width: 85%;
  margin: 7rem auto;
}
.description-title {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}
.description-text {
  width: 80%;
  margin: 2rem auto;
}
@media (min-width: 600px) {
  .description-section-wrap {
    flex: 1 1;
    padding: 0 4rem;
  }
  .description-title {
    font-size: 1.2rem;
    color: black;
  }
  .description-text {
    font-size: .8rem;
    color: black;
  }
}
 @media (min-width: 800px) {
  .description-title {
    font-size: 1.6rem;
  }
  .description-text {
    font-size: .9rem;
  }
}

@media (min-width: 700px) {
  .home-wrap {
    display: flex;
    align-items: center;
    background: white;
  }
}

.first-section-skills-wrap {
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.backgroung-skills {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.wrap-text-skills {
  width: 70%;
  position: relative;
}
.big-title-skills {
  color: white;
  opacity: 0.1;
  font-size: 4rem;
  font-weight: 900;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.title-skills {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.item {
  margin-bottom: 1rem;
  font-size: 1.7rem;
  font-family: "Poiret One";
}
@media (min-width: 700px) {
  .first-section-skills-wrap {
    height: 100vh;
    width: 70%;
  }
  .wrap-text-skills {
    width: 60%;
  }
  .big-title-skills {
    font-size: 7rem;
    top: 40%;
    left: 40%;
    line-height: 10rem;
  }
  .title-skills {
    font-size: 3rem;
  }
  .wrap-line-skills {
    width: 70%;
  }
  .item {
    font-size: 1.9rem;
  }
}

.second-section-skills-wrap {
  width: 70%;
  margin: 5rem auto;
}
.skills-title {
  font-size: 2.5rem;
  margin-bottom: 3rem;
}
.item-skills {
  font-family: "Poiret One";
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}

@media (min-width: 700px) {
  .second-section-skills-wrap {
    flex: 1 1;
    background: white;
    color: black;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.skills-wrap {
  display: flex;
  flex-direction: column;
}
@media (min-width: 700px) {
  .skills-wrap {
    flex-direction: row;
  }
}
.wrap-project {
  color: white;
  padding: 8rem 0 1rem;
  position: relative;
}
.project-title, .description-of-project {
  width: 85%;
  margin: auto;
  font-size: 2rem;
  margin-bottom: 3rem;
  font-size: 3rem;
  font-weight: 800;
  color: #c0c0c0;
}
.wrap-image-project {
  padding: 1rem 1.5rem;
  height: 430px;
}
.description-of-project {
  margin-top: 5rem;
  font-size: 1rem;
  font-weight: normal;
  list-style: none;
  color: white;
}
.link-portfolio {
  display: inline-block;
  color: white;
  text-decoration: none;
  font-family: 'Poiret One';
  font-size: 1.5rem;
}
@media (min-width: 700px) {
  .wrap-project {
    display: flex;
    align-items: center;
    padding: 0;
  }
  .wrap-image-project {
    padding: 2rem 4rem;
    height: 500px;
}
  .wrap-img-title {
    width: 70%;
    margin-top: 8rem;
    position: relative;
  }
  .description-of-project {
    height: 100vh;
    flex: 1 1;
    margin: 0;
    background: white;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 5rem;
  }
  .project-title {
    position: absolute;
    top: -20%;
    padding: 2rem 4rem;
    font-size: 3rem;
  }
  .link-portfolio{
    color: black;
  }
  .link-portfolio::after {
    content: "";
    display: block;
    width: 0;
    height: 1px;
    background: black;
    transition: width 0.3s;
  }
  .link-portfolio:hover::after {
    width: 100%;
  }
}

.li-description {
  margin-bottom: 1.5rem;
}
