* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  font-family: Raleway;
}
main {
  flex: 1 1;
  background: #001d25;
  background: #011015;
  color: white;
}

